import { Link, Outlet, useLocation } from "react-router";
import { Main } from "../../components/Main/Main";
import styles from "./Projetos.module.css";

export function ProjetosPage() {
  const name = useLocation();
  const path = name.pathname;
  return (
    <Main>
      <header className={styles.header}>
        <hr className={styles.hr_header} />
        <nav className={styles.nav_header}>
          <ul className={`${styles.ul_projetos}`}>
            <li>
              <Link to={"/projetos/anime-orange"}>AnimeOrange</Link>
            </li>
            <li>
              <Link to={"/projetos/hizus"}>Hizus</Link>
            </li>
            <li>
              <Link to={"/projetos/descriptografar"}>Descriptografar</Link>
            </li>
          </ul>
        </nav>
      </header>
      <main className={`${styles.main_projetos}`}>
        {path === "/projetos" || path === "/projetos/anime-orange" ? (
          <section className={`${styles.section_projetos}`}>
            <div>
              <h1 className={`title_green`}>AnimeOrange</h1>
              <p>
                Um site de anime contendo: pôsteres, lançamentos recentes, com
                informações específicas de cada um, podendo avançar e retroceder
                nos episódios e pesquisar por nome
              </p>
            </div>
            <div>
              <h3>Links</h3>
              <a href="http://" target="_blank" rel="noopener noreferrer">
                AnimeOrange
              </a>
              |
              <a href="http://" target="_blank" rel="noopener noreferrer">
                GitHub do Projeto
              </a>
            </div>
          </section>
        ) : (
          ""
        )}
      </main>
      <Outlet />
    </Main>
  );
}
